import React, { Component } from "react";
import image1 from "../images/pexel1.jpeg";
import image2 from "../images/pexels2.jpeg";
import { Link } from "react-router-dom";
import ImageCard from "../components/ImageCard";
import Hero from "../components/Hero";
import { CSSTransition, TransitionGroup } from "react-transition-group";
export default class Home extends Component {
  state = {
    images: [image1, image2],
    text: [
      {
        id: 1,
        heading: "Aspekt",
        paragraph:
          "Lorem ipsum dolor sit amet consectetur adipisicing elit. Aliquid, vitae dolorem tempora magnam eveniet animi deleniti laudantium dignissimos quia? Excepturi!"
      },
      {
        id: 2,
        heading: "A.B.C.D.",
        paragraph:
          "Lorem ipsum dolor, sit amet consectetur adipisicing elit. Omnis illum deserunt aliquid facere magnam magni dolore, cum ullam voluptate natus placeat similique excepturi sapiente unde?"
      }
    ],
    counter: 0
  };

  componentDidMount() {
    this.intervalId = setInterval(() => {
      this.setState({ counter: this.state.counter + 1 });
    }, 5000);
  }
  componentWillUnmount() {
    clearInterval(this.intervalId);
  }
  render() {
    return (
      <div className="showcase">
        <TransitionGroup>
          <CSSTransition
            key={
              this.state.text[this.state.counter % this.state.text.length].id
            }
            timeout={600}
            classNames="fd"
          >
            <>
              <Hero
                heading={
                  this.state.text[this.state.counter % this.state.text.length]
                    .heading
                }
                paragraph={
                  this.state.text[this.state.counter % this.state.text.length]
                    .paragraph
                }
              />
            </>
          </CSSTransition>
        </TransitionGroup>
        <div className="spacer-2"></div>
        <div className="contrast">
          <TransitionGroup>
            <CSSTransition
              key={
                this.state.text[this.state.counter % this.state.text.length].id
              }
              timeout={600}
              classNames="fd"
            >
              <ImageCard
                image={
                  this.state.images[
                    this.state.counter % this.state.images.length
                  ]
                }
              />
            </CSSTransition>
          </TransitionGroup>
        </div>
        <div className="side-bar-2">
          <Link to="/articles/asd">Sa incepem</Link>
        </div>
      </div>
    );
  }
}
