import React, { Component } from "react";
import { FaFacebookF, FaTwitter, FaInstagram } from "react-icons/fa";
import { FiFacebook, FiTwitter, FiInstagram } from "react-icons/fi";
import { Link } from "react-router-dom";
export default class Navbar extends Component {
  state = {
    open: false
  };
  toggleOpen = () => {
    this.setState({ open: !this.state.open });
  };
  render() {
    return (
      <nav>
        <div className="logo"></div>
        <div className="hamburger" onClick={this.toggleOpen}>
          <div className="line"></div>
          <div className="line"></div>
          <div className="line"></div>
        </div>
        <ul className={`nav-links ${this.state.open ? "open" : ""}`}>
          <li>
            <Link to="/" className="pill-2" onClick={this.toggleOpen}>
              Acasa
            </Link>
          </li>
          <li>
            <Link to="/404" className="pill-3" onClick={this.toggleOpen}>
              Articole (404)
            </Link>
          </li>
          <li>
            <Link
              to="articles/asd"
              className="pill-4"
              onClick={this.toggleOpen}
            >
              Afla mai mult
            </Link>
          </li>
          <li>
            {!this.state.open && (
              <div className="social-icons">
                <div>
                  <FaFacebookF />
                </div>
                <div>
                  <FaTwitter />
                </div>
                <div>
                  <FaInstagram />
                </div>
              </div>
            )}
            {this.state.open && (
              <div className="social-icons-menu">
                <div>
                  <FiFacebook />
                </div>
                <div>
                  <FiTwitter />
                </div>
                <div>
                  <FiInstagram />
                </div>
              </div>
            )}
          </li>
        </ul>
      </nav>
    );
  }
}
